.page__background {
  z-index: -1;
  top: 0 !important;
}
.page__content {
  position: static;
}
.page--home,
.page--group-select {
  display: flex;
  flex-flow: column;
}
.toolbar {
  position: static !important;
  z-index: 300;
}
.p-24 {
  padding: 24px;
  min-height: 24px;
}
.toolbar__title,
.toolbar__right,
.toolbar__left {
  width: 0;
}
.toolbar {
  flex-direction: column;
  margin-bottom: 8px;
  min-height: 72px;
  background-color: #fff;
  background-image: none;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  box-shadow: 0 4px 16px rgba(36, 37, 41, 0.1);
  color: #232323;
  white-space: inherit;
}
@media (min-width: 876px) {
  .toolbar {
    flex-flow: nowrap;
    align-items: center;
    padding: 20px 120px;
    margin-bottom: 0;
    min-height: 88px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: 0 4px 10px 0 rgba(29, 30, 32, 0.1);
  }
}
.toolbar-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toolbar-container--without-margin {
  border-radius: 0;
  margin-bottom: 0;
  box-shadow: none;
}
@media (min-width: 876px) {
  .toolbar-container__back-btn {
    display: none;
  }
}
.toolbar-container__back-btn ons-button {
  width: 48px;
  height: 48px;
  background-color: #fff;
}
.toolbar-container__back-btn ons-button svg {
  fill: #232323;
}
.toolbar-container__loading {
  height: 8px;
  overflow-x: hidden;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1), visibility 300ms linear 0ms;
}
@media (min-width: 876px) {
  .toolbar-container__loading {
    padding-top: 96px;
  }
}
.toolbar-container__loading:after {
  position: absolute;
  height: inherit;
  content: '';
  border-radius: 12px;
  background-color: rgba(255, 102, 23, 0.85);
  animation: increase 2s infinite;
}
.toolbar-container__loading:before {
  position: absolute;
  height: inherit;
  content: '';
  border-radius: 12px;
  background-color: rgba(255, 102, 23, 0.85);
  animation: decrease 2s 0.5s infinite;
}
.toolbar-container__loading--completed {
  visibility: hidden;
  opacity: 0;
}
.container-home {
  color: red;
}
.container-home__title {
  color: green;
}
ons-button[modifier~='button-main-node'] {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  flex: 1;
  padding: 0;
  height: 48px;
  color: #fff;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: bold;
  cursor: pointer;
  background-color: #26CA42;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
ons-button[modifier~='button-main-node']:first-child {
  display: flex;
  align-items: center;
  justify-content: center;
}
ons-button[modifier~='button-main-node']:active {
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  background-color: #fff;
}
